import { listRecord } from '@/api/business/groupon';
import BackToTop from '@/components/BackToTop';
import Pagination from '@/components/Pagination'; // Secondary package based on el-pagination

export default {
  name: 'GrouponActivity',
  components: { BackToTop: BackToTop, Pagination: Pagination },
  data: function data() {
    return {
      list: [],
      total: 0,
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 20,
        rulesId: undefined,
        sort: 'add_time',
        order: 'desc'
      },
      goodsDetail: '',
      detailDialogVisible: false,
      downloadLoading: false
    };
  },
  created: function created() {
    this.getList();
  },

  methods: {
    getList: function getList() {
      var _this = this;

      this.listLoading = true;
      listRecord(this.listQuery).then(function (response) {
        _this.list = response.data.data.items;
        _this.total = response.data.data.total;
        _this.listLoading = false;
      }).catch(function () {
        _this.list = [];
        _this.total = 0;
        _this.listLoading = false;
      });
    },
    handleFilter: function handleFilter() {
      this.listQuery.page = 1;
      this.getList();
    },
    handleDownload: function handleDownload() {
      var _this2 = this;

      this.downloadLoading = true;
      import('@/vendor/Export2Excel').then(function (excel) {
        var tHeader = ['商品ID', '名称', '首页主图', '折扣', '人数要求', '活动开始时间', '活动结束时间'];
        var filterVal = ['id', 'name', 'pic_url', 'discount', 'discountMember', 'addTime', 'expireTime'];
        excel.export_json_to_excel2(tHeader, _this2.list, filterVal, '商品信息');
        _this2.downloadLoading = false;
      });
    }
  }
};